*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 100%;
}

body {
  font-family: sans-serif;
  font-size: 0.9rem;
  color: #444444;
  background: #f4f9ff;
}

img {
  max-width: 100%;
}

.logo {
  width: 165px;
  line-height: 1px;
  margin-bottom: 15px;
}
.logo a {
  display: block;
}

.container {
  max-width: 1000px;
  margin: 0 auto;
}

.inner {
  max-width: 600px;
  margin: 0 auto;
}
@media (max-width: 1024px) {
  .inner {
    padding: 0 40px;
  }
}

.title {
  font-size: 1.125rem;
  font-weight: bold;
  margin-bottom: 10px;
}

/*-------------------------------------------
header
-------------------------------------------*/
#header {
  margin-top: 60px;
}
@media (max-width: 1024px) {
  #header {
    padding: 0 10px;
  }
}

/*-------------------------------------------
Mainvisual
-------------------------------------------*/
#mainvisual {
  margin-bottom: 65px;
}
@media (max-width: 1024px) {
  #mainvisual {
    padding: 0 10px;
  }
}
#mainvisual img {
  object-fit: cover;
}

/*-------------------------------------------
INDEX
-------------------------------------------*/
#index {
  background: #fff;
  padding: 30px 0;
  margin-bottom: 60px;
}
#index ol {
  margin-left: 20px;
}
#index ol li {
  margin-bottom: 20px;
}
#index ol li:last-child {
  margin-bottom: 0;
}

/*-------------------------------------------
DETAIL
-------------------------------------------*/
#detal {
  margin-bottom: 100px;
}
#detal .content {
  display: flex;
  align-items: flex-start;
}
@media (max-width: 1024px) {
  #detal .content {
    flex-direction: column;
  }
}
#detal .content img {
  width: 270px;
  margin-right: 60px;
}
@media (max-width: 1024px) {
  #detal .content img {
    width: 100%;
    margin-bottom: 25px;
  }
}
#detal .content h3 {
  font-weight: 600;
  font-size: 1.125rem;
  margin-bottom: 20px;
}
#detal .content dl {
  display: flex;
  flex-wrap: wrap;
  padding: 16px 0;
  border-top: 1px solid #888888;
  border-bottom: 1px solid #888888;
  margin-bottom: 25px;
}
#detal .content dl dt {
  width: 25%;
}
#detal .content dl dd {
  width: 75%;
}
#detal .content p {
  margin-bottom: 20px;
}
#detal .content .link {
  color: #444444;
}
#detal .content .link:hover {
  opacity: 0.8;
}

/*-------------------------------------------
FOOTER
-------------------------------------------*/
#footer {
  font-size: 0.625rem;
  padding: 15px 0;
}